<template>
  <div id="app-buttons">
    <input v-model="user_id" placeholder="Enter vk.com profile">
      <button v-on:click="get_rec_bands()">
        <span>Recommend me some cool music</span>
      </button>
  </div>
  <div class="recommended-bands">
    <p v-show="rec_bands">We recommend you to listen the following bands:</p>
      <ul id="app">
        <li v-for="band in rec_bands">
          { band }
        </li>
      </ul>
  </div>
  <div class="container">
    <router-view>
    </router-view>
  </div>
</template>

<script>
import {MuzenderAPI} from './API/MuzenderAPI'

const muzender_api = new MuzenderAPI()

export default {
  name: 'app',
  data: {
    rec_bands: '',
    user_id: '',
    loading: false
  },
  methods: {
    get_rec_bands() {
      let user = {user_id: this.user_id}
      this.loading = true
      muzender_api.get_rec_bands(user).then((response) => {
        this.rec_bands = response.data
        this.loading = false
      })
    }
  }
};
</script>



